<template>
    <div class="modal-popup edit-user-modal" @keyup.13="onSaveClick">
        <div class="mb-3 modal-popup__title">
            Редактирование пользователя

            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="#2D4C70"
                 class="bi bi-x-lg modal-popup__close"
                 viewBox="0 0 16 16"
                 @click="close"
            >
                <path
                    d="M1.293 1.293a1 1 0 0 1 1.414 0L8 6.586l5.293-5.293a1 1 0 1 1 1.414 1.414L9.414 8l5.293 5.293a1 1 0 0 1-1.414 1.414L8 9.414l-5.293 5.293a1 1 0 0 1-1.414-1.414L6.586 8 1.293 2.707a1 1 0 0 1 0-1.414z"/>
            </svg>
        </div>
        <input type="email" class="form-control mb-2" placeholder="Email address" v-model="email"
               :class="[validationClass()]" ref="input"
        >
        <div class="modal-popup__permissions">
            <label for="permissionSelect" class="modal-popup__permission-text">Права доступа</label>
            <select :size="permissions.length + 1" class="form-select" multiple
                    aria-label="multiple select" id="permissionSelect" ref="permissionSelect">
                <option v-for="(permission, i) of permissions" :key="i" :value="permission.id"
                        :selected="user.permissions.includes(permission.id)"
                >{{ permission.name }}
                </option>
            </select>
        </div>

        <ui-button class="mt-3 mb-2" @clicked="onSaveClick">Сохранить</ui-button>
        <div v-if="alert.length" class="alert alert-danger" role="alert">{{ alert }}</div>
    </div>
</template>

<script>
import {areEqual, validateEmail} from '@/utils';
import {mapActions, mapState} from 'vuex';

export default {
    props: ['user'],
    data: () => ({
        email: '',
        alert: '',
    }),
    computed: {
        permissions() {
            return this.$store.state.permissions.list;
        }
    },
    components: {
        UiButton: () => import('@/components/ui/UiButton')
    },
    mounted() {
        this.email = this.user.email;

        this.$refs.input.focus();
    },
    methods: {
        ...mapActions('users', ['updateUser']),
        resetAlert() {
            if (this.canReset) {
                this.alert = '';
                this.canReset = false;
            }
        },
        setAlert(message) {
            this.alert = message;
            setTimeout(() => {
                this.canReset = true;
            }, 250);
        },
        validationClass() {
            return (this.email === '') ? '' : (validateEmail(this.email)) ? 'email-valid' : 'email-invalid';
        },
        async onSaveClick() {
            if (!validateEmail(this.email)) {
                this.setAlert('Введен некорректный email');
                return false;
            }

            let permissions = [];

            this.$refs.permissionSelect.forEach(option => {
                if (option.selected) {
                    permissions.push(parseInt(option.value, 10));
                }
            });

            if (!permissions.length) {
                this.setAlert('Необходимо выбрать права доступа');
                return false;
            }

            let data = {};

            if (this.email !== this.user.email) {
                data.email = this.email;
            }

            if (!areEqual(permissions, this.user.permissions)) {
                data.permissions = permissions;
            }

            if (!Object.keys(data).length) {
                this.setAlert('Вы не изменили никаких данных')
                return false;
            }

            data.id = this.user.id;

            const res = await this.updateUser(data);

            if (res.error) {
                this.setAlert('Ошибка обновления пользователя');
            } else {
                this.close();
            }
        },
        close() {
            this.$modal.hide('edit-user-modal');
        }
    }
};
</script>
